body {
  background-color: var(--bcg-color);
  color: var(--text-color-pramary);
  font-family: Montserrat, sans-serif;
}

.container {
  width: 1100px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.header {
  width: 100%;
  border-bottom: 2px solid var(--bcg-color);
  position: absolute;
}

.header-menu {
  justify-content: space-between;
  display: flex;
}

.header -list {
  align-items: center;
  display: flex;
}

.header-link {
  color: var(--text-color-primary);
  padding-top: 31px;
  padding-bottom: 30px;
  text-decoration: none;
}

.header-list .headeritem + .header-item {
  margin-left: 52px;
}

.header-logo {
  color: var(--text-color-primary);
